$color-pink: #F76C6C;

#header {
  background-color: white;
  border-top: 0px;
  border-bottom: 1px solid #DDDDDD;
  box-shadow: 2px 2px 2px #AAAAAA;
  height: auto;
  margin-bottom: 50px;
  overflow: hidden;
  padding: 10px 0;
}
 
.top_title {
  display: inline;
  padding: 20px 20px;
}

#gnb {
  display: inline;
  list-style-type: none;
}

#gnb li {
  display: inline-block;
  padding: 7px 10px;
  text-align: center;
}

#gnb a:hover {
  color: $color-pink;
}